import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http'
import { Item } from '../model/item';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ItemService {

   itemNumber:number;

  constructor(private client: HttpClient) { }



  public getItemDetails(itemNumber:number): Observable<Item>{
    const httpOptions = {
      headers: new HttpHeaders({ 
        'Access-Control-Allow-Origin':'*',
      })
    };
  
    const baseurl = 'http://localhost:8080/items/getItemDetails/' + itemNumber ;
    return this.client.get<Item>(baseurl,httpOptions)
  }

  public getItems(): Observable<Item[]> {
    const httpOptions = {
      headers: new HttpHeaders({ 
        'Access-Control-Allow-Origin':'*',
      })
    };
    const baseurl = 'http://localhost:8080/items/getItems'
    return this.client.get<Item[]>(baseurl,httpOptions);
  }

  public withdrawItem(amount:number,itemNumber:number){
    const httpOptions = {
      headers: new HttpHeaders({ 
        'Access-Control-Allow-Origin':'*',
      })
    };
    console.log( amount);
    const baseurl = 'http://localhost:8080/items/withdraw/' + amount + "/" + itemNumber ;
    return this.client.put(baseurl,httpOptions);
    

  }
  public depositItem(amount:number,itemNumber:number){
    const httpOptions = {
      headers: new HttpHeaders({ 
        'Access-Control-Allow-Origin':'*',
      })
    };
    console.log( amount);
    const baseurl = 'http://localhost:8080/items/deposit/' +  amount + "/" +itemNumber ;
    return this.client.put(baseurl,httpOptions);

  }

  public addItem(item:Item): Observable<Item>{

    return this.client.post<any>('http://localhost:8080/items/addItem/', Item);
  }

  public deleteItem(itemNumber:number): Observable<Item>{
    return this.client.delete<any>('http://localhost:8080/items/deleItem/' + itemNumber);
  }
 

  public updateItem(item:Item): Observable<Item>{
    return this.client.put<Item>('http://localhost:8080/items/updateItem/',item);
  }


}
